export default function DisplayPanel({
  clickShowAppInfoInvoike = f => f,
  mainContent = '',
  clickShowDetailInvoikeLock = false,
  clickShowDetailInvoike = f => f,
  correctSubtotal = 0,
  errorSubtotoal = 0,
  ...props
}) {
  return (
    <main className='container bg-success bg-gradient rounded text-center mt-3 p-0 pt-2 text-light'>
      <div className='text-end pe-3'>
        <span className='display-6' style={{cursor: 'pointer'}} onClick={clickShowAppInfoInvoike}>⚙</span>
      </div>
      <h1 className='text-warning'>{mainContent}</h1>
      <div className='text-end mb-3'>
        <span className={'btn btn-danger' + (clickShowDetailInvoikeLock? ' disabled':'')} onClick={clickShowDetailInvoike} >詳細成績</span>
      </div>
      <section className='bg-secondary bg-gradient rounded-bottom py-3'>
        答對(Ｏ)：
        <span className="text-warning fw-bold">{correctSubtotal}</span>
        題 ／ 答錯(Ｘ)：
        <span className="text-danger fw-bold">{errorSubtotoal}</span>
        題
      </section>
    </main>
  )
}
