export const getRndInteger = (...theArgs) => (Math.floor(Math.random() * (theArgs[1] - theArgs[0] + 1 )) + theArgs[0])

export const getExamLevelIndex = (examLevelArray = []) => examLevelArray.findIndex((value, index) => value.optionStatus)

export const getMathFormul = (previoiusQuestion = '', questionLevelIndex = 0, numOfOptions = 0) => {
  let operand1, operand1StartValue, operand1EndValue, operator, operand2, operand2StartValue, operand2EndValue, question;
  switch (questionLevelIndex) {
    case 0: // level: "10以內加減法", // index: 0 (答案範圍：0~10)
      operand1StartValue = 0
      operand1EndValue = 10
      operand2StartValue = 0
      break;
    case 1: // level: "個位數加減法", // index: 1 (答案範圍：0~18)
      operand1StartValue = 0
      operand1EndValue = 9
      operand2StartValue = 0
      break;
    case 2: // level: "20以內的數加減個位數", // index: 2 (答案範圍：1~29)
      operand1StartValue = 0
      operand1EndValue = 20
      operand2StartValue = 0
      break;
    case 3: // level: "20以內的數加減20以內的數", // index: 3 (答案範圍：0~40)
      operand1StartValue = 0
      operand1EndValue = 20
      operand2StartValue = 0
      break;
    case 4: // level: "2位數加減個位數", // index: 4 (答案範圍：1~108)
      operand1StartValue = 10
      operand1EndValue = 99
      operand2StartValue = 0
      break;
    case 5: // level: "2位數加減2位數", // index: 5 (答案範圍：0~198)
      operand1StartValue = 10
      operand1EndValue = 99
      operand2StartValue = 10
      break;
    default:
      break;
  }
  // operand1 = getRndInteger(operand1StartValue, operand1EndValue)
  question = ''
  /*
  switch (questionLevelIndex) {
    case 0:
      operand2EndValue = operator? (10 - operand1) : (operand1)
      break;
    case 1:
      operand2EndValue = operator? (9) : (operand1)
      break;
    case 2:
      operand2EndValue = 9
      break;
    case 3:
      operand2EndValue = 9
      break;
    case 4:
      operand2EndValue = operator? (99) : (operand1)
      break;
    default:
      break;
  }
  operand2 = getRndInteger(operand2StartValue, operand2EndValue)
  question = operand1 + (operator? "＋" : "－") + operand2 + " ＝ ？"
  */
  while ((previoiusQuestion === question) || (question === '')) {
    operand1 = getRndInteger(operand1StartValue, operand1EndValue)
    operator = getRndInteger(0, 1) // 0 -> - ; 1 -> +
    switch (questionLevelIndex) {
      case 0: // level: "10以內加減法", // index: 0 (答案範圍：0~10)
        operand2EndValue = operator? (10 - operand1) : (operand1)
        break;
      case 1: // level: "個位數加減法", // index: 1 (答案範圍：0~18)
        operand2EndValue = operator? (9) : (operand1)
        break;
      case 2: // level: "20以內的數加減個位數", // index: 2 (答案範圍：1~29)
        operand2EndValue = (operand1 > 9)? (9) : (operand1)
        break;
      case 3: // level: "20以內的數加減20以內的數", // index: 3 (答案範圍：0~40)
        operand2EndValue = operator? (20) : operand1
        break;
      case 4: // level: "2位數加減個位數", // index: 4 (答案範圍：1~108)
        operand2EndValue = 9
        break;
      case 5: // level: "2位數加減2位數", // index: 5 (答案範圍：0~198)
        operand2EndValue = operator? (99) : (operand1)
        break;
      default:
        break;
    }
    operand2 = getRndInteger(operand2StartValue, operand2EndValue)
    question = operand1 + (operator? "＋" : "－") + operand2 + " ＝ ？"
  }
  const answer = operator? (operand1 + operand2) : (operand1 - operand2)
  const makeOptions = { [answer]: answer }
  const replyOptions = [answer]
  let optionKey
  while (replyOptions.length < numOfOptions) {
    operand1 = getRndInteger(operand1StartValue, operand1EndValue)
    operator = getRndInteger(0, 1)
    switch (questionLevelIndex) {
      case 0: // level: "10以內加減法", // index: 0 (答案範圍：0~10)
        operand2EndValue = operator? (10 - operand1) : (operand1)
        break;
      case 1: // level: "個位數加減法", // index: 1 (答案範圍：0~18)
        operand2EndValue = operator? (9) : (operand1)
        break;
      case 2: // level: "20以內的數加減個位數", // index: 2 (答案範圍：1~29)
        operand2EndValue = (operand1 > 9)? (9) : (operand1)
        break;
      case 3: // level: "20以內的數加減20以內的數", // index: 3 (答案範圍：0~40)
        operand2EndValue = operator? (20) : operand1
        break;
      case 4: // level: "2位數加減個位數", // index: 4 (答案範圍：1~108)
        operand2EndValue = 9
        break;
      case 5: // level: "2位數加減2位數", // index: 5 (答案範圍：0~198)
        operand2EndValue = operator? (99) : (operand1)
        break;
      default:
        break;
    }
    operand2 = getRndInteger(operand2StartValue, operand2EndValue)
    optionKey = operator? (operand1+operand2) : (operand1-operand2)
    // console.log(`optionKey: ${optionKey} / !makeOptions[optionKey]?`, ((!makeOptions[optionKey])? "true" : "false")) // **makeOptions[0] alway return false
    if ((!makeOptions[optionKey]) && (optionKey !== 0)) {
      makeOptions[optionKey] = optionKey
      replyOptions.push(optionKey)
    }
  }
  optionKey = getRndInteger(0, (numOfOptions - 1))
  /*
  console.log(`in lib-numOfOptions: ${numOfOptions} , question: ${question} , answer: ${answer} , optionKey: ${optionKey}` )
  console.log('in lib-makeOptions: ', makeOptions)
  console.log('in lib-original order of replyOptions: ', replyOptions)
  */
  replyOptions[0] = replyOptions[optionKey]
  replyOptions[optionKey] = answer
  // console.log('in lib-updated order of replyOptions: ', replyOptions)
  return {question, answer, replyOptions}
}

export const displayTime = time => time? (time.getHours() + ":" + ((time.getMinutes() < 10)? ("0" + time.getMinutes()) : (time.getMinutes())) + ":" + ((time.getSeconds() < 10)? ("0" + time.getSeconds()) : (time.getSeconds()))) : null

export const createArray = length => [...Array(length)]
