import Modal from 'react-bootstrap/Modal'
import Container from 'react-bootstrap/Container'

export default function AlertModal({
  show = false,
  onHide = f => f,
  animation = false,    // Default is true
  backdrop = 'static',  // Default is true
  centered = true,      // Default is false
  keyboard = false,     // Default is true
  onEnter = f => f,     // 1st order aftter set "show" as true
  onShow = f => f,      // 2nd order aftter set "show" as true
  onEntering = f => f,  // 3rd order aftter set "show" as true
  onEntered = f => f,   // 4th order aftter set "show" as true
  onExit = f => f,      // 1st order aftter call onHide function
  onExiting = f => f,   // 2nd order aftter call onHide function
  onExited = f => f,    // 3rd order aftter call onHide function
  scrollable = true,    // Default is false
  size = '',
  /*
  modalHeaderStyleClass = 'modal-header-style-class',
  modalTitleStyleClass = 'modal-title-style-class',
  */
  modalheaderstyleclass = '',
  modaltitlestyleclass = '',
  modalTitleContent = 'Default Title Content',
  /*
  modalBodyStyleClass = 'modal-body-style-class',
  modalContainerStylessClass = 'modal-container-style-class',
  */
  modalbodystyleclass = '',
  modalcontainerstylessclass = '',
  modalBodyContent = 'Default Body Content',
  /*
  modalFooterStyleClass = 'modal-footer-style-class',
  modalFooterCloseButtonStyleClass = 'btn btn-primary',
  */
  modalfooterstyleclass = '',
  modalfooterclosebuttonstyleclass = 'btn btn-primary',
  modalFooterCloseButtonContent = '回到應用程式',
  ...props
}) {
  return (
    <Modal
      show={show}
      onHide={onHide}
      animation={animation}
      backdrop={backdrop}
      centered={centered}
      keyboard={keyboard}
      onEnter={onEnter}
      onShow={onShow}
      onEntering={onEntering}
      onEntered={onEntered}
      onExit={onExit}
      onExiting={onExiting}
      onExited={onExited}
      scrollable={scrollable}
      size={size}
      {...props}
    >
      {/*
      <Modal.Header className={modalHeaderStyleClass} closeButton>
        <Modal.Title className={modalTitleStyleClass}>{modalTitleContent}</Modal.Title>
      */}
      <Modal.Header className={modalheaderstyleclass} closeButton>
        <Modal.Title className={modaltitlestyleclass}>{modalTitleContent}</Modal.Title>
      </Modal.Header>
      {/*
      <Modal.Body className={modalBodyStyleClass}>
        <Container clsssName={modalContainerStylessClass}>
      */}
      <Modal.Body className={modalbodystyleclass}>
        <Container className={modalcontainerstylessclass}>
        {
          //modalBodyContent.map(item => {retrun(<{Object.keys(item)[0]}>{item[Object.keys(item)[0]]}</{Object.keys(item)[0]}>)})
          /*
          modalBodyContent.map(item => {
            return <Object.keys(item)[0]>X</Object.keys(item)[0]>
          })
          */
          //modalBodyContent.map(item => console.log('Object.keys(item)[0]: ', Object.keys(item)[0]))
          //modalBodyContent.map((item, index) => item)
          /*
          modalBodyContent.map((item, index) => {
            return React.cloneElement(item, {key: index})
          })
          */
          modalBodyContent
        }
        </Container>
      </Modal.Body>
      {/*
      <Modal.Footer className={modalFooterStyleClass}>
        <span className={modalFooterCloseButtonStyleClass} onClick={onHide}>{modalFooterCloseButtonContent}</span>
      */}
      <Modal.Footer className={modalfooterstyleclass}>
        <span className={modalfooterclosebuttonstyleclass} onClick={onHide}>{modalFooterCloseButtonContent}</span>
      </Modal.Footer>
    </Modal>
  )
}
