import Collapse from 'react-bootstrap/Collapse'
import GridKey from './GridKey'

export default function ExamOperate({
  optionObjArray = [],
  examDetail = {},
  initialOptionIndex = 0,
  pickedOptionInvoke  = f => f,
  startExamInvoke = f => f,
  endExamInvoke = f => f,
  clickReplyInvoke = f => f
}) {
  // const initialValue = {getExamLevelIndex(optionObjArray)}
  const switchBtnClass = 'btn' +  (examDetail.startTime? ' btn-danger' : ' btn-primary')
  const switchBtnInvoke = () => (examDetail.startTime? endExamInvoke() : startExamInvoke())
  const switchBtnText = (examDetail.startTime? '結束' : '開始')
  return (
    <article className='container px-0 pt-3'>
      <div className="input-group input-group-lg mb-3">
        <span className="input-group-text">等級：</span>
        <select
          className="form-select"
          value={initialOptionIndex}
          onChange={e => pickedOptionInvoke(Number(e.target.value), initialOptionIndex, optionObjArray)}
          disabled={!!examDetail.startTime}
        >
          {
            optionObjArray.map((item, index) =>
              <option key={index} value={index}>{item.level}</option>
            )
          }
        </select>
        <span
          className={switchBtnClass}
          aria-controls='reply-option-layout'
          aria-expanded={!!examDetail.startTime}
          onClick={switchBtnInvoke}
        >
          {switchBtnText}
        </span>
      </div>
      <Collapse in={!!examDetail.startTime}>
        <div id='reply-option-layout' className='row px-2'>
          {
            examDetail.replyOptions.map((value, index) => {
              return (
                <GridKey
                  key={index}
                  buttonStyle='btn btn-outline-light btn-lg mx-1'
                  keyContent={value}
                  clickInvokeFunction={() => clickReplyInvoke(value, examDetail, initialOptionIndex)}
                />
              )
            })
          }
        </div>
      </Collapse>


    </article>
  )
}
