import React, { useState } from 'react';

import { Chart } from "react-google-charts";

import { getExamLevelIndex, getMathFormul, displayTime } from "./components/lib";
import DisplayPanel from './components/DisplayPanel'
import ExamOperate from  './components/ExamOperate'
import CopyrightNotice from './components/CopyrightNotice'
import AlertModal from './components/AlertModal'

function App() {
  const [examLevel, setExamLevel] = useState([
    { level: "10以內加減法", // index: 0 (答案範圍：0~10)
      optionStatus: true
    },
    { level: "個位數加減法", // index: 1 (答案範圍：0~18)
      optionStatus: false
    },
    { level: "20以內的數加減個位數", // index: 2 (答案範圍：1~29)
      optionStatus: false
    },
    { level: "20以內的數加減20以內的數", // index: 3 (答案範圍：0~40)
      optionStatus: false
    },
    { level: "2位數加減個位數", // index: 4 (答案範圍：1~108)
      optionStatus: false
    },
    { level: "2位數加減2位數", // index: 5 (答案範圍：0~198)
      optionStatus: false
    }
  ])
  const [examInfo, setExamInfo] = useState({
    startTime: null,
    question: '選擇等級後按下開始進行問答',
    answer: null,
    replyOptionlength: 5,
    replyOptions: [],
    lastReplyValue: null,
    lastReplyTime: null,
    correctCount: 0,
    errorCount: 0
  })
  const [alertModalProps, setAlertModalProps] = useState({
    show: false,
    animation: true,
    onHide: () => setAlertModalProps(previousProps => {return{...previousProps, show: false}}),
    onExited: f => f,
    size: '',
    modalheaderstyleclass: '',
    modalTitleContent: '',
    modalBodyContent: '',
  })

  const showAppInfoModal = () => setAlertModalProps(previous => { return {
    ...previous,
    show: true,
    size: '',
    modalheaderstyleclass: 'bg-warning text-primary',
    modalTitleContent: '使用說明',
    modalBodyContent: (
      <>
        <p>瀏覽器載入本應用程式後於畫面中間處選擇問答等級。</p>
        <p>按下「開始」後會自動鎖定問答等級選擇功能，畫面上方會出現對應等級的數學算式問答，下方會自動展開可點擊作答的按鈕選項。</p>
        <p>問答進行中，若有數學算式已經作答，顯示會自動啟用「詳細成績」按鈕提供點擊查詢已作答的詳細成績資訊。</p>
        <p>問答進行中可隨時按下「結束」按鈕結束問答，若有數學算式已經作答，會自動顯示詳細成績資訊(若未曾作答則不顯示)，關閉詳細成績資訊(按X或「回到應用程式」按鈕)後會自動閉合答題按鈕及自動停用「詳細成績」按鈕，並解鎖問答等級選擇功能。</p>
        <p>
          若有程式問題請連結：
          <a className='text-decoration-none' href='https://github.com/RayAries/grade1-mental-arithmetic/issues' target='_blank' rel="noopener noreferrer">
            <img alt='GitHub-Mark-32px' style={{height: '20px'}} src='data:image/png;base64,' />
            <span className='mx-1'>回報</span>
          </a>
          感謝您的指教。
        </p>
      </>
    )
  } } )

  const resetExamInfoAndalertModalProps = (examStatus = {}) => {
    setExamInfo({
        ...examStatus,
        startTime: null,
        question: '選擇等級後按下開始進行問答',
        answer: null,
        lastReplyValue: null,
        lastReplyTime: null,
        correctCount: 0,
        errorCount: 0
    })
    setAlertModalProps(previous => { return { ...previous, onExited: f => f} })
  }

  const showScoreDetail = (scoreDetail = {}, examLevelDetail = [], callBackInvoke = () => setAlertModalProps(previous => { return { ...previous, onExited: f => f } }) ) => {
    const examLevelInfo = examLevelDetail[getExamLevelIndex(examLevelDetail)].level
    const chartOption = {title: "問答結果",}
    const data = [
      ["ResultType", "Times"],
      ["答對(Ｏ)題數", scoreDetail.correctCount],
      ["答錯(Ｘ)題數", scoreDetail.errorCount],
    ]
    setAlertModalProps(previous => { return {
      ...previous,
      show: true,
      size: '',
      modalheaderstyleclass: 'bg-info text-warning',
      modalTitleContent: (<span className='fw-bold'>詳細成績</span>),
      modalBodyContent: (
        <>
          <Chart
            chartType="PieChart"
            data={data}
            options={chartOption}
          />
          <ul>
            <li>問答等級：{examLevelInfo}</li>
            <li>累計答題：{scoreDetail.correctCount + scoreDetail.errorCount} 題</li>
            <li>開始時間：{displayTime(scoreDetail.startTime)} </li>
            <li>最後答題時間：{displayTime(scoreDetail.lastReplyTime)} </li>
          </ul>
        </>
      ),
      onExited: () => callBackInvoke(scoreDetail)
    } })
  }

  const updateExamLevelStatus = (indexValue = 0, previoiusIndexValue = 0, previousExamLevelObjArray = []) => {
    const newExamLevelObjArray = previousExamLevelObjArray.map((valueObj, index) => {
      if ((index === indexValue) || (index === previoiusIndexValue)) valueObj.optionStatus = !valueObj.optionStatus
      return valueObj
    })
    setExamLevel(newExamLevelObjArray)
  }

  const updateExamQuestion = (updatedReplyPartOfExamStatus = {}, levelCode = 0) => {
    const newQuestion = getMathFormul(updatedReplyPartOfExamStatus.question, levelCode, updatedReplyPartOfExamStatus.replyOptionlength)
    // setExamInfo(previous => { return { ...previous, ...updatedReplyPartOfExamStatus, ...getMathFormul(previous.question, levelCode, previous.replyOptionlength) } })
    setExamInfo(previous => { return { ...previous, ...updatedReplyPartOfExamStatus, ...newQuestion } })
    // console.log('in updateExamQuestion of App-receive question: ', newQuestion)
    setAlertModalProps(previous => { return { ...previous, onExited: f => f } })
  }

  const updateExamInfo = (lastReplyValue = null, examStatus = {}, levelCode = 0) => {
    const lastReplyTime = new Date()
    let correctCount = examStatus.correctCount
    let errorCount = examStatus.errorCount
    let individualAlertModalProps = {}
    if (lastReplyValue === examStatus.answer) {
      ++correctCount
      individualAlertModalProps = {
        modalheaderstyleclass: 'bg-primary text-warning',
        modalTitleContent: '答對了(Ｏ)! 👍😁',
        modalBodyContent: (
          <>
            <h1>{examStatus.question}</h1>
            <p className='text-success'>正確答案是 {examStatus.answer}</p>
            <p className='text-primary'>你的回答是 {lastReplyValue}</p>
          </>
        )
      }
    } else {
      ++errorCount
      individualAlertModalProps = {
        modalheaderstyleclass: 'bg-danger text-black',
        modalTitleContent: '答錯了(Ｘ)! 👎😥',
        modalBodyContent: (
          <>
            <h1>{examStatus.question}</h1>
            <p className='text-success'>正確答案是 {examStatus.answer}</p>
            <p className='text-danger'>你的回答是 {lastReplyValue}</p>
          </>
        )
      }
    }
    const updatedReplyPartOfExamInfo = { ...examStatus, lastReplyValue, lastReplyTime, correctCount, errorCount }
    const commonAlertModalProps = {
      show: true,
      onExited: () => updateExamQuestion(updatedReplyPartOfExamInfo, levelCode),
      size: 'sm'
    }
    setAlertModalProps(previous => { return { ...previous, ...commonAlertModalProps, ...individualAlertModalProps } })
  }

  const startExam = (levelCode = 0, examStatus = {}) => {
    const startTime = new Date()
    const partQuestionOfExamInfo = getMathFormul('', levelCode, examStatus.replyOptionlength)
    setExamInfo({ ...examStatus, startTime, ...partQuestionOfExamInfo })
  }

  const endExam = (examStatus = {}, examLevelStatus = []) => {
    if (examStatus.lastReplyTime) {
      showScoreDetail(examStatus, examLevelStatus, () => resetExamInfoAndalertModalProps(examStatus))
    } else {
      resetExamInfoAndalertModalProps(examStatus)
    }
  }

  return (
    <>
      <DisplayPanel
        clickShowAppInfoInvoike={showAppInfoModal}
        mainContent={examInfo.question}
        clickShowDetailInvoikeLock={!examInfo.lastReplyTime}
        clickShowDetailInvoike={() => showScoreDetail(examInfo, examLevel)}
        correctSubtotal={examInfo.correctCount}
        errorSubtotoal={examInfo.errorCount}
      />

      <ExamOperate
        optionObjArray={examLevel}
        examDetail={examInfo}
        initialOptionIndex={getExamLevelIndex(examLevel)}
        pickedOptionInvoke={updateExamLevelStatus}
        startExamInvoke={() => startExam(getExamLevelIndex(examLevel), examInfo)}
        endExamInvoke={() => endExam(examInfo, examLevel)}
        clickReplyInvoke={updateExamInfo}
      />

      <CopyrightNotice styleClass='text-center text-white pt-2' />

      <AlertModal {...alertModalProps}/>
    </>
  )
}

export default App;
