export default function CopyrightNotice({
  prefix = 'Copyright',
  year = '2021',
  owner = 'Ray-Studio',
  postfix = 'All rights reserved.',
  styleClass = 'text-center',
  ownerStyleClass = 'mx-2 text-info'
}) {
  return (
    <footer className={styleClass}>
      <span>{`${prefix} @ ${year}`}</span>
      <span className={ownerStyleClass}>{owner}</span>
      <span>{`${postfix}`}</span>
    </footer>
  )
}
